import {Component, EventEmitter, forwardRef, Input, OnInit, Output} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from "@angular/forms";

/**
 * @description 
 *
 * 
 * .
 */
@Component({
    selector: 'jigsaw-mobile-switch, jm-switch',
    templateUrl: './switch.html',
    host: {
        '[class.jigsaw-switch-host]': 'true',
        '[class.jigsaw-switch-error]': '!valid'
    },
    providers: [
        {provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => JigsawMobileSwitch), multi: true},
    ]
})

export class JigsawMobileSwitch implements ControlValueAccessor, OnInit {
    @Input()
    public valid: boolean = true;

    /**
     * @internal
     */
    public _$content: any; // .

    /**
     * .()
     */
    @Input()
    public onLabel: any;

    /**
     * ()
     */
    @Input()
    public offLabel: any;

    /**
     * size  'default'  small
     *
     */
    @Input()
    public size: string = 'default';

    private _checked: boolean = false;

    /**
     *  false;
     *
     */
    @Input()
    public get checked(): boolean {
        return this._checked
    };

    public set checked(value: boolean) {
        this.writeValue(value);
        this._propagateChange(value);
    }

    /**
     * , checked .
     *
     */
    @Output() public checkedChange = new EventEmitter<boolean>();

    /**
     * ,
     *
     */
    @Output() public change = this.checkedChange;

    private _disabled: boolean = false;

    /**
     *   boolean  false;
     *
     */
    @Input()
    public get disabled(): boolean {
        return this._disabled;
    };

    public set disabled(value: boolean) {
        this._disabled = value;
        this._setSwitchClass();
    }

    /**
     * @internal
     */
    public _$switchClick() {
        if (!this.disabled) {
            this.checked = !this.checked;

            // 
            this.checkedChange.emit(this.checked);
            this._propagateChange(this.checked);
        }
    }

    /**
     * @internal
     */
    public _$switchClass: {};

    /**
     * 
     */
    private _setSwitchClass() {
        this._$switchClass = {
            'jigsaw-switch': 'true',
            'jigsaw-switch-small': this.size === 'small',
            'jigsaw-switch-checked': this.checked,
            'jigsaw-switch-disabled': this.disabled
        }
    }

    /**
     * .
     */
    private _setInnerValue() {
        this._$content = this.checked ? this.onLabel : this.offLabel;
    }

    public ngOnInit() {
        this._setSwitchClass();
        this._setInnerValue();
    }

    private _propagateChange: any = () => {
    };

    public writeValue(value: any): void {
        this._checked = !!value;
        this._setSwitchClass();
        this._setInnerValue();
    }

    public registerOnChange(fn: any): void {
        this._propagateChange = fn;
    }

    public registerOnTouched(fn: any): void {
    }
}
